<template>
  <div class="test-container">
    <h2>{{title}}</h2>
    <ul>
      <li v-for="l in listArr">{{l.title}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      listArr: [],
      title: ""
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      this.axios
        // new_movies
        //subject/26825664/photos
        .get("/movie/new_movies", {
          params: {
            count: 10,
            apikey: "0df993c66c0c636e29ecbb5344252a4a"
          }
        })
        .then(res => {
          console.log(res.data);
          this.listArr = res.data.subjects;
          this.title = res.data.title;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.test-container {
  text-align: center;
  ul {
    li {
      line-height: 0.6rem;
      background: rgba(0, 0, 0, 0.2);
      color: #ffffff;
      border-bottom: 1px solid #ccc;
    }
  }
}
</style>
